.main-form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 80vh;
    color: black
}

.main-form form {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 30%;
    flex-direction: column;
    background-color: white;
    padding: 40px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
        rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 5px;
    gap: 20px;
}

.main-form form h1 {
    font-size: 35px;
    font-weight: 300px;
    color: var(--error);
}

.main-form form .field {
    width: 100%;

}

.main-form form label {
    display: block;
    font-size: 17px;

}

.main-form form input {
    display: block;
    outline: none;
    border: 1px solid black;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
}

.main-form form button {
    border: none;
    background-color: var(--error);
    color: white;
    outline: none;
    padding: 10px 30px;
    font-size: 15px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s;
}

.main-form form button:hover {
    transform: scale(1.05);
}