@import url("https://fonts.googleapis.com/css2?family=Poppins : wght@300; 400; 500; 600; 800&family=VT323&display=swap");

/* layout */
:root {
    --primary: #1aac83;
    --error: #e7195a;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    background: #f1f1f1;
    font-family: "Poppins";
}

a {
    text-decoration: none;
}